import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import { Subscription } from 'rxjs/Subscription';
import { MatDialog, MatDialogRef } from '@angular/material';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

import { InventoryFormDialogComponent } from './inventory-form/inventory-form.component';
import { InventoryService } from './inventory.service';

@Component({
    selector     : 'fuse-inventory',
    templateUrl  : './inventory.component.html',
    styleUrls    : ['./inventory.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseInventoryComponent implements OnInit, OnDestroy
{
    hasSelectedInventory: boolean;
    selectedCount = 0;
    searchInput: FormControl;
    dialogRef: any;
    onSelectedInventoryChangedSubscription: Subscription;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

    constructor(
        private inventoryService: InventoryService,
        public dialog: MatDialog
    )
    {
        this.searchInput = new FormControl('');
    }

    ngOnInit()
    {
        this.onSelectedInventoryChangedSubscription =
            this.inventoryService.onSelectedInventoryChanged
                .subscribe(selectedInventory => {
                    this.selectedCount = selectedInventory.length;
                });

        this.searchInput.valueChanges
            .debounceTime(300)
            .distinctUntilChanged()
            .subscribe(searchText => {
                this.inventoryService.onSearchTextChanged.next(searchText);
            });
    }

    ngOnDestroy()
    {
        this.onSelectedInventoryChangedSubscription.unsubscribe();
    }

    newInventory()
    {
        this.dialogRef = this.dialog.open(InventoryFormDialogComponent, {
            panelClass: 'inventory-form-dialog',
            data      : {
                action: 'new'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }

                this.inventoryService.updateInventory(
                    response.getRawValue()
                );

            });

    }

    deleteSelectedInventory()
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete all selected inventory?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.inventoryService.deleteSelectedInventory();
            }
            this.confirmDialogRef = null;
        });
    }
}



