import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';

import {
    MatButtonModule,
    MatCheckboxModule,
    MatNativeDateModule,
    MatDatepickerModule,
    MatDialogModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatPaginatorModule,
    MatRippleModule,
    MatSidenavModule,
    MatSortModule,
    MatTableModule,
    MatToolbarModule
} from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseConfirmDialogModule } from '@fuse/components';

import { FuseInventoryComponent } from './inventory.component';
import { InventoryService } from './inventory.service';
import { FuseInventoryInventoryListComponent } from './inventory-list/inventory-list.component';
import { InventoryFormDialogComponent } from './inventory-form/inventory-form.component';

const routes: Routes = [
    {
        path     : '**',
        component: FuseInventoryComponent,
        resolve  : {
            inventory: InventoryService
        }
    }
];

@NgModule({
    declarations   : [
        FuseInventoryComponent,
        FuseInventoryInventoryListComponent,
        InventoryFormDialogComponent
    ],
    imports        : [
        RouterModule.forChild(routes),
        CdkTableModule,

        MatButtonModule,
        MatCheckboxModule,
        MatNativeDateModule,
        MatDatepickerModule,
        MatDialogModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatPaginatorModule,
        MatRippleModule,
        MatSidenavModule,
        MatSortModule,
        MatTableModule,
        MatToolbarModule,

        FuseSharedModule,
        FuseConfirmDialogModule
    ],
    providers      : [
        InventoryService
    ],
    entryComponents: [InventoryFormDialogComponent]
})
export class FuseInventoryModule
{
}



