import { Component, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MatPaginator, MatSort } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { InventoryFormDialogComponent } from '../inventory-form/inventory-form.component';

import { InventoryService } from '../inventory.service';

import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { Inventory } from '../inventory.model';

@Component({
    selector     : 'fuse-inventory-inventory-list',
    templateUrl  : './inventory-list.component.html',
    styleUrls    : ['./inventory-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseInventoryInventoryListComponent implements OnInit, OnDestroy
{
    @ViewChild('dialogContent') dialogContent: TemplateRef<any>;
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;

    inventory: any;
    dataSource: InventoryDataSource | null;
    displayedColumns = ['id', 'img', 'name', 'category', 'price', 'quantity', 'active'];
    selectedInventory: any[];
    checkboxes: {};

    onInventoryChangedSubscription: Subscription;
    onSelectedInventoryChangedSubscription: Subscription;

    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    dialogRef: MatDialogRef<InventoryFormDialogComponent>;

    constructor(
        private inventoryService: InventoryService,
        private router: Router,
        public dialog: MatDialog
    )
    {
        this.onInventoryChangedSubscription =
            this.inventoryService.onInventoryChanged.subscribe(inventory => {

                this.inventory = inventory;

                this.checkboxes = {};
                inventory.map(inventory => {
                    this.checkboxes[inventory.id] = false;
                });
            });

        this.onSelectedInventoryChangedSubscription =
            this.inventoryService.onSelectedInventoryChanged.subscribe(selectedInventory => {
                for ( const id in this.checkboxes )
                {
                    if ( !this.checkboxes.hasOwnProperty(id) )
                    {
                        continue;
                    }

                    this.checkboxes[id] = selectedInventory.includes(id);
                }
                this.selectedInventory = selectedInventory;
            });

    }

    ngOnInit()
    {
        this.dataSource = new InventoryDataSource(
            this.inventoryService,
            this.paginator,
            this.sort
        );
    }

    ngOnDestroy()
    {
        this.onInventoryChangedSubscription.unsubscribe();
        this.onSelectedInventoryChangedSubscription.unsubscribe();
    }

    editInventory(inventory)
    {
        this.dialogRef = this.dialog.open(InventoryFormDialogComponent, {
            panelClass: 'inventory-form-dialog',
            data      : {
                inventory: inventory,
                action : 'edit'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.inventoryService.updateInventory(
                            formData.getRawValue()
                        );

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteInventory(inventory);

                        break;
                }
            });
    }

    /**
     * Delete Inventory
     */
    deleteInventory(inventory)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.inventoryService.deleteInventory(inventory);
            }
            this.confirmDialogRef = null;
        });

    }

    onSelectedChange(inventoryId)
    {
        this.inventoryService.toggleSelectedInventory(inventoryId);
    }

    toggleAll(ev) {
        if (ev) {
            this.inventoryService.toggleSelectAll();
        }
    }
}

export class InventoryDataSource extends DataSource<any>
{
    total = 0;

    private sub1: Subscription;

    constructor(
        private inventoryService: InventoryService,
        private _paginator: MatPaginator,
        private _sort: MatSort
    ) {
        super();
    }

    /** Connect function called by the table to retrieve one stream containing the data to render. */
    connect(): Observable<Inventory[]>
    {
        const displayDataChanges = [
            this.inventoryService.onInventoryChanged,
            this._paginator.page,
            this._sort.sortChange
        ];


        this.sub1 = this.inventoryService.onInventoryChanged
            .subscribe(inventory => {
                setTimeout(() => {
                    this.total = inventory.length;
                });
            });

        return Observable.merge(...displayDataChanges)
            .map(() => {
                let inventory = this.inventoryService.inventory;

                inventory = this.sortData(inventory);

                const startIndex = this._paginator.pageIndex * this._paginator.pageSize;
                inventory = inventory.slice(startIndex, startIndex + this._paginator.pageSize);

                return inventory;
            });
    }

    sortData(data): Inventory[] {
        if ( !this._sort.active || this._sort.direction === '' )
        {
            return data;
        }

        return data.sort((a, b) => {
            let valueA, valueB;

            switch(this._sort.active) {
                case 'created':
                    valueA = new Date(a[this._sort.active]);
                    valueB = new Date(b[this._sort.active]);
                    break;
                case 'name':
                    valueA = a['firstname'] + ' ' + a['lastname'];
                    valueB = b['firstname'] + ' ' + b['lastname'];
                    break;
                default:
                    valueA = a[this._sort.active];
                    valueB = b[this._sort.active];
                    break;
            }

            return (valueA < valueB ? -1 : 1) * (this._sort.direction === 'asc' ? 1 : -1);
        });
    }

    disconnect()
    {
        this.sub1.unsubscribe();
    }
}



