import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';

import { FuseUtils } from '@fuse/utils';

import { Inventory } from './inventory.model';

@Injectable()
export class InventoryService implements Resolve<any>
{
    onInventoryChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSelectedInventoryChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSearchTextChanged: Subject<any> = new Subject();
    onFilterChanged: Subject<any> = new Subject();

    inventory: Inventory[];
    selectedInventory: string[] = [];

    searchText: string;
    filterBy: string;

    constructor(private http: HttpClient)
    {
    }

    /**
     * The Inventory App Main Resolver
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {

            Promise.all([
                this.getInventory(),
            ]).then(
                ([files]) => {

                    this.onSearchTextChanged.subscribe(searchText => {
                        this.searchText = searchText;
                        this.getInventory();
                    });

                    this.onFilterChanged.subscribe(filter => {
                        this.filterBy = filter;
                        this.getInventory();
                    });

                    resolve();

                },
                reject
            );
        });
    }

    getInventory(): Promise<any>
    {
        return new Promise((resolve, reject) => {
                this.http.get('api/inventory')
                    .subscribe((response: any) => {

                        this.inventory = response;

                        if ( this.searchText && this.searchText !== '' )
                        {
                            this.inventory = FuseUtils.filterArrayByString(this.inventory, this.searchText);
                        }

                        this.inventory = this.inventory.map(inventory => {
                            return new Inventory(inventory);
                        });

                        this.onInventoryChanged.next(this.inventory);
                        resolve(this.inventory);
                    }, reject);
            }
        );
    }

    /**
     * Toggle selected inventory by id
     * @param id
     */
    toggleSelectedInventory(id)
    {
        // First, check if we already have that todo as selected...
        if ( this.selectedInventory.length > 0 )
        {
            const index = this.selectedInventory.indexOf(id);

            if ( index !== -1 )
            {
                this.selectedInventory.splice(index, 1);

                // Trigger the next event
                this.onSelectedInventoryChanged.next(this.selectedInventory);

                // Return
                return;
            }
        }

        // If we don't have it, push as selected
        this.selectedInventory.push(id);

        // Trigger the next event
        this.onSelectedInventoryChanged.next(this.selectedInventory);
    }

    /**
     * Toggle select all
     */
    toggleSelectAll()
    {
        if ( this.selectedInventory.length === this.inventory.length )
        {
            this.deselectInventory();
        }
        else
        {
            this.selectInventory();
        }
    }

    selectInventory(filterParameter?, filterValue?)
    {
        this.selectedInventory = [];

        // If there is no filter, select all todos
        if ( filterParameter === undefined || filterValue === undefined )
        {
            this.selectedInventory = [];
            this.inventory.map(inventory => {
                this.selectedInventory.push(inventory.id);
            });
        }
        else
        {
            /* this.selectedInventory.push(...
                 this.inventory.filter(todo => {
                     return todo[filterParameter] === filterValue;
                 })
             );*/
        }

        // Trigger the next event
        this.onSelectedInventoryChanged.next(this.selectedInventory);
    }

    updateInventory(inventory)
    {
        return new Promise((resolve, reject) => {

            this.http.post('api/receivings/' + inventory.id, {...inventory})
                .subscribe(response => {
                    this.getInventory();
                    resolve(response);
                });
        });
    }

    deselectInventory()
    {
        this.selectedInventory = [];

        // Trigger the next event
        this.onSelectedInventoryChanged.next(this.selectedInventory);
    }

    deleteInventory(inventory)
    {
        const inventoryIndex = this.inventory.indexOf(inventory);
        this.inventory.splice(inventoryIndex, 1);
        this.onInventoryChanged.next(this.inventory);
    }

    deleteSelectedInventory()
    {
        for ( const inventoryId of this.selectedInventory )
        {
            const inventory = this.inventory.find(_inventory => {
                return _inventory.id === inventoryId;
            });
            const inventoryIndex = this.inventory.indexOf(inventory);
            this.inventory.splice(inventoryIndex, 1);
        }
        this.onInventoryChanged.next(this.inventory);
        this.deselectInventory();
    }

}



